<template>
  <div>
    <div class="row py-1">
      <div class="col-8 col-md-10">
        <h4 class="ms-4">Client</h4>
      </div>
      <div class="col-4 col-md-2">
        <router-link to="/reseller/business-list">
          <v-btn tile color="cred">
            <v-icon left>
              mdi-format-list-bulleted
            </v-icon>
            LIST
          </v-btn>
        </router-link>
      </div>
    </div>
    <v-tabs v-model="tab">
      <v-tab cols="6">Update Business Client</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-card color="basil" flat >
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-row>
              <v-col class="py-4" cols="12" md="12">
                <v-text-field
                    v-model="bdata.name"
                    :rules="nameRules"
                    :counter="100"
                    dense
                    label="Store / Business Name" type="text">
                </v-text-field>
              </v-col>
              <v-col class="py-2" cols="12" md="12">
                <v-text-field
                    v-model="bdata.email"
                    :rules="emailRules"
                    @keyup="emailVerify"
                    :error-messages="emailVerifyMessage"
                    label="Your Email"
                    dense
                    type="email">
                </v-text-field>
              </v-col>
              <v-col class="py-2" cols="12" md="12">
                <v-text-field
                    v-model="bdata.username"
                    :rules="usernameRules"
                    @keyup="userNameVerify"
                    :error-messages="userNameVerifyMessage"
                    :counter="80"
                    dense
                    label="Username"
                    type="text">
                </v-text-field>
              </v-col>
              <v-col class="py-2" cols="12" md="12">
                <v-text-field label="GST" v-model="bdata.gst"></v-text-field>
              </v-col>
              <!-- <v-col class="py-2" cols="12" md="12">
                <v-text-field
                    v-model="password"
                    :rules="passwordRules"
                    :counter="15"
                    dense
                    label="Enter Password"
                    type="password">
                  ></v-text-field>
              </v-col> -->
              <!-- <v-col class="py-2" cols="12" md="12">
                <v-text-field
                    v-model="confirm_password"
                    :rules="[(password === confirm_password) || 'Confirm password does not match']"
                    :counter="15"
                    dense
                    label="Confirm Password"
                    type="password">
                </v-text-field>
              </v-col> -->
              <v-col class="d-flex" cols="12" md="12">
                <v-autocomplete
                    v-model="bdata.cat_id"
                    @change="AllCats"
                    :rules="maincatRuless"
                    label="Select Main Category"
                    :items="allmaincategories"
                    item-text="cat_name"
                    item-value="id"
                    dense
                    prepend-inner-icon="mdi-tray-arrow-down">
                </v-autocomplete>
              </v-col>
              <v-col cols="12" md="12">
                <v-autocomplete
                    v-model="subids"
                    :rules="catRuless"
                    label="Select Sub Category"
                    :items="allcategories"
                    item-text="subcat_name"
                    multiple
                    item-value="id"
                    dense
                    :no-data-text="customNoDataText1"
                    prepend-inner-icon="mdi-tray-arrow-down">
                </v-autocomplete>
              </v-col>
              <v-col class="d-flex" cols="12" md="12">
                <v-autocomplete dense
                                v-model="bdata.state_id"
                                @change="getCities"
                                :rules="stateRules"
                                label="Select State"
                                :items="allstates"
                                item-text="state"
                                prepend-inner-icon="mdi-home-city"
                                item-value="id">
                </v-autocomplete>
              </v-col>
              <v-col class="d-flex" cols="12" md="12">
                <v-autocomplete dense
                                v-model="bdata.city_id"
                                :rules="cityRules"
                                label="Select City"
                                :items="cities"
                                item-text="city"
                                prepend-inner-icon="mdi-city"
                                item-value="id"
                                :no-data-text="customNoDataText">
                </v-autocomplete>
              </v-col>
              <v-col class="py-2" cols="12" md="12">
                <v-text-field
                    v-model="bdata.mobile_phone"
                    :rules="mobileRules"
                    @keyup="phoneVerify"
                    dense
                    :error-messages="phoneVerifyMessage"
                    :counter="10"
                    label="Enter Mobile"
                    type="number">
                </v-text-field>
              </v-col>
              <v-col class="py-2" cols="12" md="12">
                <v-textarea rows="3" v-model="bdata.address" dense row-height="20" label="Business Address"></v-textarea>
              </v-col>
              <v-col class="py-2 ms-2" cols="12" md="2">
                <v-checkbox v-model="bdata.user_status" label="Active"></v-checkbox>
              </v-col>
            </v-row>
            <div class="text-end">
              <v-btn :disabled="!valid" @click="BusinessformSubmit" color="cred" large class="text-white">SAVE</v-btn>
            </div>
          </v-form>
          <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
            <template v-slot:action="{attrs}">
              <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
            </template>
          </v-snackbar>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>

import axios from "axios";

export default {
  name: 'UpdateBusinesslist',
  props: {
    business_id: Number,
  },
  components: {},
  data() {
    return{
      bdata: [],
      subids: [],
      tab: '',
      allcategories:[],
      allmaincategories:[],
      allstates:[],
      cities:[],
      userNameVerifyMessage:'',
      emailVerifyMessage:'',
      phoneVerifyMessage:'',
      valid: false,
      snackbar: false,
      text: '',
      timeout: '',
      customNoDataText: 'Please Select State First',
      customNoDataText1: 'Please Select Main Category First',
      nameRules: [
        v => !!v || 'Store/Bussine Name is required.',
        v => v.length <= 100 || 'Name must be less than 100 characters.',
      ],
      usernameRules: [
        v => !!v || 'Username is required.',
        v => v.length <= 80 || 'Username must be less than 80 characters.',
        v => (v.split(' ').length <= 1) || 'space not allowed',
        v => /^[\w\s]+$/.test(v) || 'Username may only contain letters, numbers, dashes and underscores'
      ],
      catRuless: [
        v => !!v || 'Sub Category name is required.',
      ],
      maincatRuless: [
        v => !!v || 'Main Category name is required.',
      ],
      mobileRules: [
        v => !!v || 'Mobile number is required.',
        v => v.length < 9 ||  'Mobile number must be 10 characters.',
      ],
      stateRules: [
        v => !!v || 'State is required.',
      ],
      cityRules: [
        v => !!v || 'City is required.',
      ],
      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
      // passwordRules: [
      //   v => !!v || 'Password is required.',
      //   v => /[A-Z]/.test(v) || 'Use upper case.',
      //   v => /[a-z]/.test(v) || 'Use lower case.',
      //   v => /[0-9]/.test(v) || 'Use number.',
      //   v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
      //   v => v.length >= 8 || 'Password must be greater than 8 characters.',
      //   v => v.length <= 15 || 'Password must be less than 15 characters.',
      // ],
      addressRules: [
        v => !!v || 'Address is required.',
        v => v.length <= 100 || 'Address must be less than 100 characters.',
      ],
      agree_policyRules: [
        v => !!v || 'This field is required.',
      ],
    }
  },
  mounted() {
    this.Getbusinessdata();
    this.AllStates();
    this.allmaincats();
  },

  methods: {
    Getbusinessdata(){
      axios.get(`get/business/details?business_id=${this.business_id}`)
          .then((resp) =>{
            this.bdata = resp.data.registered_business[0];
            this.AllCats();
            for (let i = 0; i < resp.data.subcatids.length; i++) {
              this.subids.push(
                  resp.data.subcatids[i].id
              );
            }
            this.getCities();
          });
    },
    AllCats() {
        axios.get('subcategories/'+this.bdata.cat_id)
            .then((resp) => {
              this.allcategories = resp.data;
            })
    },
    allmaincats() {
      axios.get('maincategory')
          .then((resp) => {
            this.allmaincategories = resp.data.maincategories;
          });
    },
    AllStates(){
      axios.get('allstates')
          .then((resp)=>{
            this.allstates = resp.data.locations;
          });
    },
    getCities() {
        axios.get('states/'+this.bdata.state_id)
            .then(response => {
              // console.log('cities '+response.data);
              this.cities = response.data;
            })
            .catch(error => {
              alert(error);
              console.log(error);
            });
    },

    userNameVerify(){ // verify username
      if(this.bdata.username.length >= 8){
        axios.post('user/verify', {'username':this.bdata.username})
            .then((resp) => {
              if(resp.data.status==400){
                this.userNameVerifyMessage = resp.data.message;
              }else{
                this.userNameVerifyMessage = "";
              }
            });
      }
    },
    emailVerify(){ // verify email
      if(/.+@.+/.test(this.bdata.email)){
        axios.post('user/verify', {'email':this.bdata.email})
            .then((resp) => {
              if(resp.data.status==400){
                this.emailVerifyMessage = resp.data.message;
              }else{
                this.emailVerifyMessage = "";
              }
            });
      }
    },
    phoneVerify(){ // verify phone
      if(this.bdata.mobile_phone.length == 10){
        axios.post('user/verify', {'mobile_phone':this.bdata.mobile_phone})
            .then((resp) => {
              if(resp.data.status==400){
                this.phoneVerifyMessage = resp.data.message;
              }else{
                this.phoneVerifyMessage = "";
              }
            });
      }
    },

    async BusinessformSubmit() {

      var data = {
        business_id: this.business_id,
        business_name: this.bdata.name,
        email: this.bdata.email,
        username: this.bdata.username,
        mobile: this.bdata.mobile_phone,
        user_status: this.bdata.user_status,
        // password: this.password,
        category: this.bdata.cat_id,
        subcategories: this.subids,
        GST: this.bdata.gst,
        city: this.bdata.city_id,
        state: this.bdata.state_id,
        Address: this.bdata.address
      };
      axios.post('update/reseller/details', data)
          .then((resp) => {
            console.log(resp.message);
            this.snackbar = true;
            this.text = resp.data.message;
            this.Getbusinessdata();
            this.timeout = 3000;
          });
      // const config = {
      //   headers: { 'content-type': 'multipart/form-data' }
      // }
      // await axios.post('register/reseller', this.bdata, config)
      //     .then((resp) => {
      //       this.snackbar = true;
      //       this.text = resp.data.message;
      //       this.timeout = 3000;
      //       this.$refs.form.reset();
      //     }).catch(error => {
      //       // var data = {...error.response};
      //       var data = error.toJSON();
      //       if(data.status == 400){
      //         this.snackbar = true;
      //         this.text = "something went wrong.";
      //         this.timeout = 3000;
      //       }
      //     });
      // this.$refs.form.validate();
    },
  },
}
</script>